@import url('https://fonts.googleapis.com/css2?family=Bona+Nova+SC:ital,wght@0,400;0,700;1,400&display=swap');

body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-bottom: 50px; /* AÃ±ade espacio para el footer */
}

h1 {
  font-family: "Bona Nova SC", serif;
  font-optical-sizing: auto;
  font-size: 3em;
  color: #000;
  margin-bottom: 20px;
  text-align: center;
}

button {
  padding: 10px 20px;
  margin: 5px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.2em;
}

button.primary-button  {
  border: #02b686 1px solid;
  background-color: #02b686;
  color: white;
}

button:hover.primary-button  {
  border: #057557 1px solid;
  background-color: #057557;
}

button:hover.secondary-button {
  background-color: #f7f7f7;
}

button.stop-button {
  border: rgb(200, 2, 61) 1px solid;
  color: rgb(200, 2, 61);
}

button:hover.stop-button {
  background-color: rgb(200, 2, 61);
  color: white;
}